.home {
    max-width: 1500px;
    margin: 0 auto;
}
.home__image {
    max-width: 1600px;
    /* width: 3500px; */
    max-height: 100%;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
    z-index: -1;
    margin-bottom: -150px;
}

.home__row {
    display: grid;
    max-width: 1400px;
    width: 100%;
    grid-template-columns: auto auto auto auto;
    grid-auto-flow: dense;
    z-index: 999 !important;
    justify-content: space-evenly;
    /* flex-wrap: wrap; */
    align-items: center;
    margin: 0 auto;
}

