.dropdown-menus {
  width: 500px;
  min-height: 400px;
  position: absolute;
  top: 122px;
  right: 175px;
  list-style: none;
  text-align: start;
  z-index: 10;
  display: flex;
  background-color: #fff;
  color: #000;
  padding: 10px 30px 10px 10px;
  /* justify-content: space-between; */
  align-items: flex-start;
}
.itemss {
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  background-color: #fff !important;
}

.dropdown-menus .list {
  /* margin-right: 15px; */
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.dropdown-menus li {
  margin: 0;
  background: #fff;
  color: #000;
  cursor: pointer;
}

.dropdown-menus.clicked {
  display: none;
}

.dropdown-links {
  font-size: 14px;
  display: block;
  width: 100%;
  text-decoration: none;
  height: 100%;
  color: #000;
  padding: 2px 16px;
}

.dropdown-link:hover {
  text-decoration: underline #ee8802;
  color: #ee8802;
}

.clips {
  position: absolute;
  background-color: #fff;
  width: 50px;
  height: 50px;
  clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  right: 603px;
  top: 114px;
}

.dropdown-menu a {
  text-decoration: none;
}
