.payment {
  background: #fff;
  height: 100%;
}
.payment__container {
  background: #fff;
  display: flex;
  justify-content: center;
  max-width: 1920px;
  margin: 0 160px;
}

.payment__container > h1 a {
  text-decoration: none;
}

.payment__containerLeft {
  width: 720px;
  flex: 0.7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.payment__containerRight {
  width: 360px;
  height: 100%;
  border: 1px solid gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-items: center; */
  justify-content: flex-start;
  margin-left: 60px;
  margin-top: 150px;
  padding: 16px;
}

.payment__containerRight .btn {
  width: 100%;
  padding: 5px 0;
  border: 1px outset gray;
  color: #111;
  background-color: #facc8f;
  border-radius: 4px;
  outline: none;
  transition: all 0.1s ease-in;
  margin-bottom: 16px;
  cursor: pointer;
}
.payment__containerRight .btn:hover {
  background-color: #e2a759;
}
.payment__containerRight .btn:visited {
  background-color: #fff;
}
.payment__priceContainer .payment__cal {
  display: flex;
  justify-content: space-between;
}
.payment__priceContainer > hr {
  margin-bottom: 5px;
}
.payment__containerLeft > img {
  margin: 32px 0;
}

.payment__section {
  display: flex;
  justify-content: space-around;
  width: 720px;
  border: 1px solid gray;
  padding: 16px;
  margin: 16px 0;
}

.add__info {
  flex: 0.3;
}
.method__info {
  flex: 0.4;
  margin-left: -16px;
}
.gift__info {
  flex: 0.3;
  margin-left: 12px;
}

.gift__point > input {
  margin-right: 5px;
  padding: 5px 0;
  outline: none;
  border: 1px solid gray;
  border-radius: 4px;
}
.gift__point > input:focus {
  border-color: #cd9042;
}

.gift__point > button {
  cursor: pointer;
}

.payment__details {
}
.payment__section .icon {
  margin: 0 16px;
}

.payment__section .notification {
}

.payment__sectionItem {
  width: 720px;
  border: 1px solid gray;
  padding: 16px;
  margin: 16px 0;
  display: flex;
  flex-direction: column;
}

.payment__sectionItem {
  width: 720px;
  border: 1px solid gray;
  padding: 16px;
  margin: 16px 0;
  display: flex;
}
