.dropdown-menu {
  width: 500px;
  min-height: 400px;
  position: absolute;
  top: 58px;
  right: 100px;
  list-style: none;
  text-align: start;
  z-index: 10;
  display: flex;
  background-color: #fff;
  color: #000;
  padding: 10px 30px 10px 10px;
  /* justify-content: space-between; */
  align-items: flex-start;
}
.items {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  background-color: #fff !important;
}

.dropdown-menu .list {
  border-bottom: 1px dotted gray;
  margin-right: 15px;
}

.dropdown-menu .title {
  padding-left: 20px;
}
.items2 {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  /* padding-right: -50px; */
  /* text-align: center; */
  border-left: 1px dotted gray;
}
.dropdown-menu li {
  margin: 0;
  background: #fff;
  cursor: pointer;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  font-size: 14px;
  display: block;
  width: 100%;
  text-decoration: none;
  height: 100%;
  color: #000;
  padding: 2px 16px;
}

.dropdown-link:hover {
  text-decoration: underline #ee8802;
  color: #ee8802;
}

.logout {
  font-size: 14px;
  display: block;
  width: 100%;
  text-decoration: none;
  height: 100%;
  color: #000;
  padding: 2px 16px;
}

.clip {
  position: absolute;
  background-color: #fff;
  width: 50px;
  height: 50px;
  clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  right: 207px;
  top: 52px;
}

.dropdown-menu a {
  text-decoration: none;
}
