.login {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;

  justify-content: space-between;
  height: 100vh;
}
.login__top {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 60vh;

  /* justify-content: space-between; */
}
.login__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;

  border-top: 2px solid gray;
  background-color: rgb(233, 237, 237);
  width: 100%;
  height: 100%;
  padding-top: 20px;
}

.logo {
  max-width: 200px;
  width: 100%;
  margin: 20px;
}

.login__main {
  max-width: 320px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  /* justify-items: center; */
  /* background: red; */
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid gray;
}
.login__main > h2 {
  margin-bottom: 16px;
}

.login__main > form > input {
  width: 96%;
  padding: 7px;
  border: 1px inset gray;
  margin-bottom: 16px;
  border-radius: 4px;
}
.login__main > form > input:focus {
  border: 2px solid #cd9042;
  outline: 0;
}

.login__main > form > button {
  width: 100%;
  padding: 5px 0;
  border: 1px outset gray;
  color: #111;
  background-color: #facc8f;
  border-radius: 4px;
  outline: none;
  transition: all 0.1s ease-in;
  margin-bottom: 16px;
}
.login__main > form > button:hover {
  background-color: #e2a759;
  cursor: pointer;
}

.login__main > small > a {
  text-decoration: none;
}

.login__main > small {
  margin-bottom: 16px;
}

.login__top > small {
  color: gray;
}
.login__top > button {
  width: 100%;
  margin-top: 14px;
  margin-bottom: 32px;
}

.btn1 {
  text-decoration: none;
  width: 100%;
  padding: 0 130px;
}

.login__bottom .support {
  width: 20%;
  display: flex;
  justify-content: space-evenly;
}

.login__bottom > small {
  font-size: 12px;
}
.support > a {
  text-decoration: none;
  font-size: 12px;
  color: rgb(0, 174, 255);
  margin-bottom: 16px;
}
