.navbar__right {
  flex: 0.8;
}
.navbar {
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
}

.class > h2 {
  color: #000;
  padding-left: 16px;
}

.class > hr {
  margin-bottom: 16px;
}

.MuiSvgIcon-root {
  color: #fff;
}

.nav-menu {
  background-color: #fff;
  width: 450px;
  /* height: 100vh; */

  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  scroll-behavior: auto;
  scrollbar-width: thin;
}

.nav-menu.active {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  left: 0;
  transition: 250ms;
  z-index: 1000;
  overflow-x: hidden;
  overflow-y: unset;
  text-align: justify;

  height: 100vh;
}

.nav-menu.active > ul {
  margin-top: 70px;
}

.nav-menur {
  background-color: rgba(0, 0, 0, 0.8);
  width: -100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  /* transform: translateX(100%); */
  top: 0;
  opacity: 0;
  right: 0;
  transition: all 250ms ease;
  z-index: -1 !important;
  cursor: auto;
}

.nav-menur.active {
  width: 100%;
  /* transform: translateX(0%); */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  opacity: 1;
  transition: all 300ms ease;
  z-index: 999 !important;
}

.nav-menu.active > h2 {
  padding: 10px !important;
  text-align: center;
  background-color: #060b26;
  width: 430px;
  position: fixed;
}
/* item text */
.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  color: #060b26;
}

.nav-text a {
  text-decoration: none;
  /* color: #f5f5f5; */
  color: #060b26 !important;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;

  margin-left: -14px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: rgb(233, 237, 237);
  /* color: #fff !important; */
}

.nav-menu-items {
  margin-top: 16px;
  width: 100%;
  background: #fff;
}

.navbar-toggle {
  position: absolute;
  background: transparent;
  width: 66%;
  /* transform: scale(1.6); */

  top: 28px;
  right: calc(100% - 500px);
  color: #fff;
  position: fixed;
  height: 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  z-index: 999;
}

.nav-drop {
  position: absolute;
  transform: translateX(100%);
  top: 100px;
  width: 100%;
  color: #000;
  transition: all 0.5s ease;
}

.nav-drop.active {
  transform: translateX(0%);
}

.dropdownss {
  position: absolute;
  top: 100px;
  width: 300px;
  transform: translateX(-45%);
  background-color: #000;
  border: 1px solid #000;
  padding: 1rem;
  overflow: hidden;
  color: #fff;
}
