.checkout {
  display: flex;
  justify-content: space-around;
}
.checkout__left {
  width: 100%;
  flex: 0.8;
  max-width: 1920px;
}
.checkout__right {
  max-width: 300px;
  right: 0;
  width: 100%;
  flex: 0.1;
  margin: 10px 20px;
}
.checkout__ad {
  width: 100%;
  height: 100px;
  object-fit: cover;
  margin: 10px 0;
}

.checkout__info {
  padding: 32px 16px;
  margin: 16px;
  background-color: #fff;
}

.info__header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;

  margin-top: -10px;
}
.info__header > h1 {
  color: #000;
  margin-top: -10px;
}
.info__header > small {
  padding-bottom: 10px;
}
.info__header > p {
  margin-bottom: -10px;
}
.info__header .p {
  text-decoration: none;
  color: rgb(0, 126, 147);
}

.info__header .p:hover {
  border-bottom: 1px solid rgb(200, 80, 31);
  color: rgb(200, 80, 31);
}
