@import url('https://fonts.googleapis.com/css2?family=Kosugi+Maru&display=swap');

* {
  /* box-sizing: border-box; */
  margin: 0;
  padding: 0;
  /* font-family: ; */
}

.headers {
  position: sticky;
  z-index: 999;
}

.header {
  background-color: rgb(20, 25, 32);
  display: flex;
  align-items: center;

  /* position: sticky; */
  top: 0;
  z-index: 100;
}
.header__logo {
  object-fit: contain;
  height: 40px;
  filter: invert(1);
  position: relative;
  cursor: pointer;
  margin: 18px 20px;
}
.logo_arrow {
  height: 70px;
  position: absolute;
  left: 29.5px;
  top: 15.8px;
  transform: rotate(0.7deg) scale(1.008);
  z-index: 10;
}
.header__address {
  display: flex;
  align-items: flex-end;
  margin: 0 10px;
  padding: 10px 10px 10px 10px;
}
.header__address:hover {
  border: 1px solid #fff;
  cursor: pointer;
}
.header__addressIcon {
  transform: scaleX(1.2);
  color: #fff;
  /* margin-right: -12px; */
  margin-bottom: 2px;
}

.header__search {
  display: flex;
  min-width: 200px;
  flex: 1;
}

.header__searchInput {
  border: 1px solid gray;
  height: 12px;
  padding: 10px;
  border-radius: 5px 0 0 5px;
  width: 100%;
}
.header__searchInput:focus {
  border-color: #cd9042;
  outline: none;
}
.header__searchIcon {
  background-color: #cd9042;
  padding: 6px;
  margin-right: 10px;
  height: 22px !important;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.header__optionBasket {
  display: flex;

  align-items: center;
  padding: 10px;
}
.header__optionBasket:hover {
  border: 1px solid #fff;
  cursor: pointer;
}

.header__basketCount {
  margin: 0 10px;
  color: #cd9042;
  font-weight: 800;
  margin-right: 20px;
}

.header__nav {
  display: flex;
  justify-content: space-between;
}

.header__option {
  display: flex;
  flex-direction: column;
  color: #fff;
  margin: 0 10px;
}

.dropdown {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__link:hover {
  border: 1px solid #fff;
  cursor: pointer;
}

.header__option > span {
  font-size: 12px;
  margin-bottom: -5px;
}
.header__option > strong {
  font-weight: 800;
}
.header__link {
  color: #fff;
  text-decoration: none;
  /* margin: 0 10px; */
  padding: 10px;
}

/* Header bottom */

.header__bottom {
  display: flex;
  justify-content: space-between;
  color: #fff;
  background-color: rgb(35, 47, 62);

  align-items: center;
}

.header__bottom .header__nav {
  display: flex;
  align-items: center;
  padding: 5px;
}

.header__bottom .header__nav a {
  text-decoration: none;
  color: #fff;
}

.header__bottom .header__nav > p {
  color: #e29a3c;
  font-weight: 600;
}
.header__bottom .header__nav:hover {
  border: 1px solid #fff;
  cursor: pointer;
}
