.checkoutProduct {
  display: flex;
  margin: 10px;
}
.checkoutProduct > img {
  object-fit: contain;
  width: 150px;
  height: 150px;
}
.checkoutProduct__info {
  padding-left: 20px;
}

.checkoutProduct__info > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
