.nav-menu {
  background-color: #fff;
  width: 450px;
  /* height: 100vh; */

  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  scroll-behavior: auto;
  scrollbar-width: thin;
}

.nav-menu.active {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  left: 0;
  transition: 250ms;
  z-index: 1000;
  overflow-x: hidden;
  overflow-y: unset;
  text-align: justify;

  height: 100vh;
}

.nav-menu.active > ul {
  margin-top: 70px;
}

.nav-menur {
  background-color: rgba(0, 0, 0, 0.8);
  width: -100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  /* transform: translateX(100%); */
  top: 0;
  opacity: 0;
  right: 0;
  transition: all 250ms ease;
  z-index: -1 !important;
  cursor: auto;
}

.nav-menur.active {
  width: 100%;
  /* transform: translateX(0%); */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  opacity: 1;
  transition: all 300ms ease;
  z-index: 999 !important;
}

.nav-menu.active > h2 {
  padding: 10px !important;
  text-align: center;
  background-color: #060b26;
  width: 430px;
  position: fixed;
}
