.slider {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.right__arrow {
  position: absolute;
  top: 50%;
  right: 32px;
  transform: scale(2);
  color: #fff;
  z-index: 10;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.left__arrow {
  position: absolute;
  top: 50%;
  left: 32px;
  transform: scale(2);
  z-index: 10;
  color: #fff;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.slide {
  opacity: 0.5;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.03) translateY(1%);
}

