.orders__order {
  padding: 20px 80px;
}
.orders__header {
  display: flex;
  padding: 10px;
  margin: 30px auto;
  width: 720px;
  height: 100%;
  background: #fff;
  box-shadow: 0px 0 2px 1px rgb(0, 199, 0);
}
.done {
  color: rgb(0, 160, 0);
  margin: 0 10px;
}
h1 {
  color: rgb(0, 160, 0);
}

.order {
  padding: 40px;
  margin: 20px 0;
  border: 1px solid lightgray;
  position: relative;
}

.order__id {
  position: absolute;
  top: 40px;
  right: 20px;
}

.order__total {
  font-weight: 500;
  text-align: right;
}

