.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px;
  min-width: 320px;
  height: 440px;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.05),
    inset -5px -5px 5px rgba(255, 255, 255, 0.5), 5px 5px 5px rgba(0, 0, 0, 0.5),
    -5px -5px 5px rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  position: relative;
  margin: 30px;
  z-index: 100 !important;
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  max-height: 400px;
  min-width: 100px;
  margin: 10px;
  padding: 30px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  z-index: 100 !important;
  border-radius: 15px;
  transition: 0.5s;
}

.product:hover {
  transform: translateY(-15px) scale(1.01);
  /* background: linear-gradient(45deg, #b95ce4, #4f29cd); */
}

.product .card {
}

.product__info {
  transition: 0.5s;
  width: 100%;
  height: 100px;
  margin-bottom: 15px;
}

.product__price {
  margin-top: 5px;
}

.product__rating {
  display: flex;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product .btn {
  border-radius: 8px;
  padding: 0 10px;
  height: 32px;
  background-color: #f0c14b;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  margin: 0 auto;
  transition: all 0.2s ease;
}

.product .btn:hover {
  background: #ff568f;
  color: #fff;
  font-weight: 800;
  cursor: pointer;
}
